import React, { FC, useEffect, useMemo, useState } from 'react';
import { client } from '../../../App';
import { GET_CDN_WIDGETS } from '../../../queries/widgets';
import { Tab, useMediaQuery } from '@mui/material';
import theme from '../../../theme';
import { WidgetBuilder } from './widget-builder';
import { Box, Tabs } from '../styles';

const tabThemes = [
  { color: '#ccd5df', class: 'theme_01' },
  { color: '#D0BB8C', class: 'theme_02' },
  { color: '#467097', class: 'theme_01 theme_03' },
  { color: '#894e23', class: 'theme_01 theme_04' },
  { color: '#cd7309', class: 'theme_01 theme_05' },
];

export interface IThemeColorSet {
  primary: string;
  secondary: string;
  accent: string;
  background: string;
  surface: string;
  text: string;
  textSecondary: string;
  border: string;
  shadow: string;
  gradientColors: string[];
}

export interface ITheme {
  _id?: string;
  name: string;
  mainColor: string;
  colorSet: IThemeColorSet;
  isActive?: boolean;
  createdAt?: string;
  updatedAt?: string;
}

export const CdnWidgets: FC<{ apiKey: string }> = ({ apiKey }) => {
  const [tabs, setTabs] = useState([]);
  const [activeTabId, setActiveTabId] = useState<number | string>('');
  const [color, setColor] = useState<string>('');
  const [mode, setMode] = useState<'tab' | 'single'>('tab');
  const [singleContents, setSingleContents] = useState([]);
  const [isin, setIsin] = useState('');
  const [widgetTheme, setWidgetTheme] = useState<any>(null);

  // Check window size
  const isXs = useMediaQuery(theme.breakpoints.only('xs'));
  const isSm = useMediaQuery(theme.breakpoints.down('md'));

  const fetchData = async (apiKey: string) =>
    client.query({
      query: GET_CDN_WIDGETS,
      variables: { widgetId: apiKey },
    });

  const themeClass = useMemo(() => {
    if (widgetTheme && widgetTheme.mainColor.toLowerCase() === '#d0bb8c') {
      return 'theme_02';
    }

    if (color) {
      return tabThemes.find((tabTheme) => {
        return tabTheme.color.toLowerCase() === color?.toLowerCase();
      })?.class;
    } else {
      return tabThemes[0].class;
    }
  }, [color, widgetTheme]);

  useEffect(() => {
    fetchData(apiKey).then((res) => {
      const data = JSON.parse(res?.data?.getCdnWidgets?.data) || [];

      setIsin(data.isin);
      setTabs(data?.tabs || []);
      setColor(data?.color);
      setActiveTabId(data?.tabs ? data?.tabs[0]?.id : '');
      setMode(data?.mode || 'tab');
      setSingleContents(data?.contents);
      if (data?.theme) {
        setWidgetTheme(data.theme);
      }
    });
  }, [apiKey]);

  const handleTabChange = (_: React.SyntheticEvent, activeId: number) => {
    setActiveTabId(activeId);
  };

  return mode === 'tab' ? (
    <Box className={`box_${themeClass}`}>
      <Tabs
        className={`_Tabs ${themeClass}`}
        sx={widgetTheme ? {
          background: widgetTheme.colorSet.background,
          '& .MuiTabs-flexContainer': {
            button: {
              color: widgetTheme.colorSet.textSecondary,
              '&:hover': {
                color: `${themeClass === 'theme_02' ? widgetTheme.colorSet.textSecondary : widgetTheme.colorSet.text} !important`,
              },
              '&.Mui-selected': {
                color: `${widgetTheme.colorSet.text} !important`,
                borderBottomColor: `${widgetTheme.colorSet.text} !important`
              }
            },
          },
        } : {}}
        value={activeTabId}
        orientation={isSm ? 'vertical' : 'horizontal'}
        onChange={handleTabChange}
      >
        {tabs.map(({ label, id }, index) => (
          <Tab sx={{ fontWeight: 400 }} key={index} value={id} label={label} />
        ))}
      </Tabs>
      {tabs.map(({ id, content }, index) =>
        activeTabId === id ? (
          <WidgetBuilder
            key={index}
            type="container"
            content={content}
            color={widgetTheme?.mainColor || color}
            isin={isin}
            theme={widgetTheme}
          />
        ) : null
      )}
    </Box>
  ) : (
    <>
      <WidgetBuilder type="container" content={singleContents} isin={isin} color={color} />
    </>
  );
};
